<template>
  <v-app>
    <!-- App Bar -->
    <v-app-bar
      fixed
      dark
      app
    >
      <v-btn @click="$router.go(-1)" icon>
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
      <v-app-bar-title v-if="animes !== null" style="width: 100%;">{{ genre }}</v-app-bar-title>
      <v-skeleton-loader v-if="animes === null" type="heading" width="100%"></v-skeleton-loader>
    </v-app-bar>

    <!-- Main -->
    <v-main>
      <!-- Container -->
      <v-container fluid>
        <v-row v-if="animes !== null">
          <v-col v-for="anime in animes" cols="12" md="6" :key="anime.id">
            <router-link :to="{ path: '/anime/'+encodeURI(anime.id) }">
              <v-card class="anime">
                <v-img :src="anime.cover" class="anime-image rounded" />
                <b class="anime-title">{{ anime.title }}</b>
                <div class="anime-details">
                  <span class="item">Tipe : {{ anime.type }}</span>
                  <span class="item">Rating : {{ anime.rate }}</span>
                  <span class="item">Status : {{ anime.status }}</span>
                </div>
              </v-card>
            </router-link>
          </v-col>
        </v-row>

        <v-row v-if="animes === null || loadAnimes === true">
          <v-col cols="12" md="6">
            <v-card class="anime">
              <v-skeleton-loader
                type="image"
                class="anime-image rounded"
              ></v-skeleton-loader>
              <v-skeleton-loader
                type="article"
                class="anime-title"
              ></v-skeleton-loader>
            </v-card>
          </v-col>

          <v-col cols="12" md="6">
            <v-card class="anime">
              <v-skeleton-loader
                type="image"
                class="anime-image rounded"
              ></v-skeleton-loader>
              <v-skeleton-loader
                type="article"
                class="anime-title"
              ></v-skeleton-loader>
            </v-card>
          </v-col>

          <v-col cols="12" md="6">
            <v-card class="anime">
              <v-skeleton-loader
                type="image"
                class="anime-image rounded"
              ></v-skeleton-loader>
              <v-skeleton-loader
                type="article"
                class="anime-title"
              ></v-skeleton-loader>
            </v-card>
          </v-col>

          <v-col cols="12" md="6">
            <v-card class="anime">
              <v-skeleton-loader
                type="image"
                class="anime-image rounded"
              ></v-skeleton-loader>
              <v-skeleton-loader
                type="article"
                class="anime-title"
              ></v-skeleton-loader>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<style scoped>
.anime {
  height: 160px;
}

.anime-image {
  width: 120px;
  height: 160px;
  float: left;
}

.anime-title {
  padding: 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: block;
  font-weight: bold;
  font-size: 18px;
}

.anime-details {
  padding: 10px;
  display: block;
  margin-left: 120px;
}

.anime-details > .item {
  font-weight: 500;
  font-size: 14px;
  display: block;
}
</style>

<script>
import Vue from "vue";
import axios from "axios";

export default Vue.extend({
  name: "Genre",
  data() {
    return {
      genre_id: this.$route.params.genre_id,
      genre: this.$route.params.genre_id.toUpperCase().replaceAll('-', ' '),
      animes: null,
      nextAnimes: '',
      loadAnimes: false,
    }
  },
  methods: {
    scroll() {
      window.onscroll = async () => {
        let bottomOfWindow = Math.max(window.pageYOffset, document.documentElement.scrollTop, document.body.scrollTop) + window.innerHeight === document.documentElement.offsetHeight;
        if (bottomOfWindow && this.nextAnimes !== '' && this.loadAnimes === false) {
          await this.loadAnime(this.nextAnimes);
        }
      }
    },
    async loadAnime(page = '') {
      if (this.animes !== null) {
        this.loadAnimes = true;
        await axios.get("https://api.abpay.xyz/api/genre?api_key=b988b287-c0ff-408c-b941-9619f283db76&genre_id="+this.genre_id+"&page="+page)
          .then(res => {
            this.nextAnimes = res.data.data.nextPage;
            for (let i = 0; i < res.data.data.data.length; i++) {
              this.animes.push(res.data.data.data[i]);
            }
          }).catch(err => {
            console.log(err);
          });
        
        this.loadAnimes = false;
      }
    }
  },
  async created() {
    await axios.get("https://api.abpay.xyz/api/genre?api_key=b988b287-c0ff-408c-b941-9619f283db76&genre_id="+this.genre_id)
      .then(res => {
        this.animes = res.data.data.data;
        this.nextAnimes = res.data.data.nextPage;
      }).catch(err => {
        console.log(err);
      });
    
    this.scroll();
  },
});
</script>